import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { logout } from "../store/authSlice";
import "./Sidebar.css";
import {
  Users,
  LayoutDashboard,
  ClipboardMinus,
  LogOut,
  Menu,
  Bell,
} from "lucide-react";

const Sidebar = ({ isCollapsed, toggleSidebar }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleLogout = () => {
    dispatch(logout());
    navigate("/login");
  };

  return (
    <div
      className="bg-light text-black d-flex flex-column p-3"
      style={{
        width: isCollapsed ? "80px" : "250px",
        transition: "width 0.3s",
        // height: "100vh", // REMOVE THIS LINE
        minHeight: "100vh", // Optional: ensures it takes at least 100vh if content is short
        position: "fixed", // THIS IS KEY: Fixes the sidebar to the viewport
        top: 0, // Align to the top of the viewport
        left: 0, // Align to the left of the viewport
        zIndex: 1000, // Optional: ensure it stays above other content
      }}
    >
      <div className="d-flex justify-content-between align-items-center mb-4">
        {!isCollapsed && <h4 className="m-0 gilroy-bold">Collabrinth</h4>}
        <button className="btn btn-sm " onClick={toggleSidebar}>
          <Menu className="me-2" style={{ width: "20px", height: "20px" }} />
        </button>
      </div>

      <ul className="nav flex-column">
        <li className="nav-item mb-2">
          <button
            className="btn btn-link text-black text-start w-100 gilroy-semi-bold"
            onClick={() => navigate("/dashboard")}
          >
            <LayoutDashboard
              className="me-2"
              style={{ width: "20px", height: "20px" }}
            />
            {!isCollapsed && "Dashboard"}
          </button>
        </li>
        <li className="nav-item mb-2">
          <button
            className="btn btn-link text-black text-start w-100 gilroy-semi-bold"
            onClick={() => navigate("/users")}
          >
            <Users className="me-2" style={{ width: "20px", height: "20px" }} />
            {!isCollapsed && "Users"}
          </button>
        </li>
        <li className="nav-item mb-2">
          <button
            className="btn btn-link text-black text-start w-100 gilroy-semi-bold"
            onClick={() => navigate("/all-gigs")}
          >
            <ClipboardMinus
              className="me-2"
              style={{ width: "20px", height: "20px" }}
            />
            {!isCollapsed && "All Gigs"}
          </button>
        </li>
        <li className="nav-item mb-2">
          <button
            className="btn btn-link text-black text-start w-100 gilroy-semi-bold"
            onClick={() => navigate("/notifications")}
          >
            <Bell className="me-2" style={{ width: "20px", height: "20px" }} />
            {!isCollapsed && "Notifications"}
          </button>
        </li>
      </ul>

      {/* Spacer */}
      <div className="flex-grow-1" />

      {/* Logout at the bottom */}
      <button
        className="btn text-white text-start w-100 gilroy-semi-bold mt-3"
        style={{ backgroundColor: "#dc9428" }}
        onClick={handleLogout}
      >
        <LogOut className="me-2" style={{ width: "20px", height: "20px" }} />
        {!isCollapsed && "Logout"}
      </button>
    </div>
  );
};

export default Sidebar;
