import React, { useState } from "react";
import "./RoleCard.css";
import { ChevronDown, ChevronUp } from "lucide-react";

const RoleCard = ({ data }) => {
  const [isExpanded, setIsExpanded] = useState(false);

  const getDurationDisplay = (durationCode) => {
    switch (durationCode) {
      case "<1Day":
        return "less than a day";
      case "1Week":
        return "less than a week";
      case "1Month":
        return "1 month";
      case ">1Month":
        return "more than a month";
      default:
        return "the specified duration";
    }
  };

  const toggleExpand = () => {
    setIsExpanded(!isExpanded);
  };

  return (
    <div className="role-card" onClick={toggleExpand}>
      <div className="role-card-header">
        <div style={{ display: "flex", alignItems: "center", gap: "10px" }}>
          <h5 className="role-title gilroy-semi-bold">{data?.title}</h5>
          {/* Conditional rendering of the dot based on isLive */}
          <span
            className={`status-dot ${data?.isLive ? "live" : "closed"}`}
          ></span>
        </div>
        {isExpanded ? (
          <ChevronUp size={24} className="icon" />
        ) : (
          <ChevronDown size={24} className="icon" />
        )}
      </div>
      <p className="role-subtitle gilroy-medium">
        {data?.subType}. {data?.gender?.join(", ")}. {data?.minAge}-
        {data?.maxAge}.
      </p>
      <p className="role-title gilroy-medium">APPLICATIONS</p>

      {isExpanded && (
        <>
          <hr className="my-3" />

          <h6 className="role-title gilroy-medium">ABOUT THE GIG</h6>
          <p className="role-subtitle gilroy-medium">
            TYPE: <span className="info-text">{data?.subType}</span>
            <br />
            GENDER:{" "}
            <span className="info-text">{data?.gender?.join(", ")}</span>
            <br />
            AGE RANGE:{" "}
            <span className="info-text">
              {data?.minAge}-{data?.maxAge}
            </span>
            <br />
            ETHNICITY: <span className="info-text">{data?.ethnicity}</span>
          </p>

          {data?.roleDescription && (
            <>
              <h6 className="section-title gilroy-medium">DESCRIPTION</h6>
              <p className="info-text">{data?.roleDescription}</p>
            </>
          )}

          {data?.skills?.length > 0 && (
            <>
              <h6 className="section-title mt-3 gilroy-medium">
                SKILLS NEEDED
              </h6>
              <p className="info-text">
                {data?.skills.map((skill) => skill.name).join(", ")}
              </p>
            </>
          )}

          {data?.languages?.length > 0 && (
            <>
              <h6 className="section-title mt-3 gilroy-medium">
                LANGUAGES NEEDED
              </h6>
              <p className="info-text">
                {data?.languages.map((language) => language.name).join(", ")}
              </p>
            </>
          )}

          {data?.isPaid &&
            (data?.amount || data?.amount === 0) &&
            data?.duration && (
              <p className="info-text text-success mt-3">
                <span className="font-poppins"></span>
                {data.amount}{" "}
                {data.rateType === "flat"
                  ? ""
                  : data.rateType || "per session/hour"}{" "}
                for the work duration of {getDurationDisplay(data.duration)}.
              </p>
            )}
        </>
      )}
    </div>
  );
};

export default RoleCard;
