import React, { useEffect, useState } from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
} from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import { setCredentials } from "./store/authSlice";
import { getTokens } from "./utils/tokenStorage";
import Login from "./pages/Login";
import Dashboard from "./pages/Dashboard";
import "./App.css";
import Users from "./pages/Users";
import UserProfile from "./pages/UserProfile";
import Notifications from "./pages/Notifications";
import Gigs from "./pages/Gigs";
import AllGigs from "./pages/AllGigs";
import LandingPage from "./pages/website/LandingPage";
import PrivacyPolicy from "./pages/website/PrivacyPolicy";
import ContactUs from "./pages/website/ContactUs";
import TermsOfService from "./pages/website/TermsOfService";

const App = () => {
  const dispatch = useDispatch();
  const accessToken = useSelector((state) => state.auth.accessToken);
  const isAuthenticated = !!accessToken;
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const { accessToken, refreshToken } = getTokens();
    if (accessToken && refreshToken) {
      dispatch(setCredentials({ accessToken, refreshToken, user: null }));
    }
    setLoading(false);
  }, [dispatch]);

  if (loading) return <div>Loading...</div>;

  return (
    <Router>
      <Routes>
        {/* Redirect logged-in users away from login 
        <Route path="/" element={<ComingSoon />} />*/}
        <Route path="/" element={<LandingPage />} />
        <Route path="/privacy-policy" element={<PrivacyPolicy />} />
        <Route path="/terms-of-service" element={<TermsOfService />} />
        <Route path="/contact" element={<ContactUs />} />
        <Route
          path="/login"
          element={isAuthenticated ? <Navigate to="/dashboard" /> : <Login />}
        />
        <Route
          path="/dashboard"
          element={isAuthenticated ? <Dashboard /> : <Navigate to="/login" />}
        />
        <Route
          path="/users"
          element={isAuthenticated ? <Users /> : <Navigate to="/login" />}
        />
        <Route
          path="/all-gigs"
          element={isAuthenticated ? <AllGigs /> : <Navigate to="/login" />}
        />
        <Route
          path="/notifications"
          element={
            isAuthenticated ? <Notifications /> : <Navigate to="/login" />
          }
        />
        <Route
          path="/users/:id"
          element={isAuthenticated ? <UserProfile /> : <Navigate to="/login" />}
        />
        <Route
          path="/gigs"
          element={isAuthenticated ? <Gigs /> : <Navigate to="/login" />}
        />
        {/* Redirect all other paths based on auth status */}
        <Route
          path="*"
          element={
            isAuthenticated ? (
              <Navigate to="/dashboard" />
            ) : (
              <Navigate to="/login" />
            )
          }
        />
      </Routes>
    </Router>
  );
};

export default App;
