// src/components/VerificationModal.jsx (Updated)
import React from "react";

const VerificationModal = ({
  show,
  onClose,
  onApprove, // New prop for approve action
  onReject, // New prop for reject action
  password,
  onPasswordChange,
  error,
  galleryImage, // New prop for gallery image
  cameraImage, // New prop for camera image
  verificationStatus, // New prop for status
}) => {
  if (!show) return null;

  return (
    <div
      className="modal fade show d-block"
      tabIndex="-1"
      role="dialog"
      style={{ backgroundColor: "rgba(0,0,0,0.5)" }}
    >
      <div
        className="modal-dialog modal-dialog-centered modal-lg"
        role="document"
        style={{ maxWidth: "800px" }} // restricts modal width
      >
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">User Verification</h5>
            <button
              type="button"
              className="btn-close"
              onClick={onClose}
              aria-label="Close"
            ></button>
          </div>

          <div className="modal-body">
            <p>
              Current Status: <strong>{verificationStatus || "N/A"}</strong>
            </p>

            {/* Image layout using Bootstrap grid */}
            <div className="row g-3 mt-2">
              <div className="col-12 col-md-6 text-center">
                {galleryImage ? (
                  <img
                    src={galleryImage}
                    alt="Gallery Proof"
                    className="img-fluid rounded"
                    style={{
                      maxHeight: "300px",
                      objectFit: "contain",
                      border: "1px solid #eee",
                    }}
                  />
                ) : (
                  <div
                    className="text-muted border p-4 rounded"
                    style={{
                      minHeight: "200px",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    No Gallery Image
                  </div>
                )}
                <p className="small mt-1">Gallery Image</p>
              </div>
              <div className="col-12 col-md-6 text-center">
                {cameraImage ? (
                  <img
                    src={cameraImage}
                    alt="Selfie Proof"
                    className="img-fluid rounded"
                    style={{
                      maxHeight: "300px",
                      objectFit: "contain",
                      border: "1px solid #eee",
                    }}
                  />
                ) : (
                  <div
                    className="text-muted border p-4 rounded"
                    style={{
                      minHeight: "200px",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    No Selfie Image
                  </div>
                )}
                <p className="small mt-1">Selfie Image</p>
              </div>
            </div>

            <div className="mb-3 mt-4">
              <label
                htmlFor="verificationAdminPassword"
                className="form-label visually-hidden"
              >
                Admin Password
              </label>
              <input
                type="password"
                className={`form-control ${error ? "is-invalid" : ""}`}
                id="verificationAdminPassword"
                value={password}
                onChange={onPasswordChange}
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    // Decide which action to trigger if Enter is pressed
                    // For simplicity, we'll leave it to manual clicks for now.
                    // Or you could add an "active" button state.
                  }
                }}
                placeholder="Enter password to confirm"
              />
              {error && <div className="invalid-feedback">{error}</div>}
            </div>
          </div>

          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Cancel
            </button>
            <button
              type="button"
              className="btn btn-danger"
              onClick={onReject} // Use the new onReject prop
              disabled={!password || error} // Disable if no password or error
            >
              Reject
            </button>
            <button
              type="button"
              className="btn btn-success"
              onClick={onApprove} // Use the new onApprove prop
              disabled={!password || error} // Disable if no password or error
            >
              Approve
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default VerificationModal;
