// src/components/AdminConfirmationModal.jsx
import React from "react";
import { Lock, Unlock } from "lucide-react"; // Assuming you still want icons within the modal logic

const AdminConfirmationModal = ({
  show,
  onClose,
  onConfirm,
  password,
  onPasswordChange,
  error,
  targetIsAdminStatus, // Whether the user is becoming admin or not
}) => {
  if (!show) return null;

  return (
    <div
      className="modal fade show d-block"
      tabIndex="-1"
      role="dialog"
      style={{ backgroundColor: "rgba(0,0,0,0.5)" }}
    >
      <div className="modal-dialog modal-dialog-centered" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">Confirm Admin Status Change</h5>
            <button
              type="button"
              className="btn-close"
              onClick={onClose}
              aria-label="Close"
            ></button>
          </div>
          <div className="modal-body">
            <p>
              Enter the admin password to{" "}
              <strong>{targetIsAdminStatus ? "make" : "remove"}</strong> this
              user an admin.
            </p>
            <div className="mb-3">
              <label
                htmlFor="adminPasswordConfirm"
                className="form-label visually-hidden"
              >
                Admin Password
              </label>
              <input
                type="password"
                className={`form-control ${error ? "is-invalid" : ""}`}
                id="adminPasswordConfirm"
                value={password}
                onChange={onPasswordChange}
                onKeyPress={(e) => {
                  if (e.key === "Enter") {
                    onConfirm();
                  }
                }}
                placeholder="Enter password"
              />
              {error && <div className="invalid-feedback">{error}</div>}
            </div>
          </div>
          <div className="modal-footer">
            <button
              type="button"
              className="btn btn-secondary"
              onClick={onClose}
            >
              Cancel
            </button>
            <button
              type="button"
              className="btn btn-primary"
              onClick={onConfirm}
            >
              Confirm
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default AdminConfirmationModal;
