import React, { useEffect, useState, useMemo } from "react";
import Sidebar from "../components/Sidebar";
import apiClient from "../api/apiClient";
import { useNavigate } from "react-router-dom";
import routes from "../network/routes";

const Notifications = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedUserType, setSelectedUserType] = useState("");
  const [isAdminFilter, setIsAdminFilter] = useState("");
  const navigate = useNavigate();

  // New state for selected user IDs
  const [selectedUserIds, setSelectedUserIds] = useState(new Set());
  const [notificationTitle, setNotificationTitle] = useState("");
  const [notificationBody, setNotificationBody] = useState("");

  const toggleSidebar = () => setIsCollapsed((prev) => !prev);

  // --- Fetch Users ---
  const fetchUsers = async () => {
    setLoading(true);
    try {
      const res = await apiClient(routes.pushToken);
      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || "Failed to fetch users");
      }
      const data = await res.json();
      console.log("Fetched users:", data.data); // Log fetched users for debugging

      setUsers(data.data);
    } catch (error) {
      console.error("Error fetching users:", error.message);
      // Optionally show a general error message to the user
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const getDisplayRole = (role) => {
    switch (role?.toLowerCase()) {
      case "actor":
        return "Talent";
      case "casting":
        return "Casting";
      default:
        return "N/A";
    }
  };

  const getRoleBadgeClass = (role) => {
    switch (role?.toLowerCase()) {
      case "actor":
        return "bg-warning text-dark";
      case "casting":
        return "bg-info text-dark";
      default:
        return "bg-secondary text-white";
    }
  };

  // Define sidebar widths to use for margin
  const sidebarWidthCollapsed = 80;
  const sidebarWidthExpanded = 250;

  // --- Filtering Logic ---
  const filteredUsers = useMemo(() => {
    let currentUsers = [...users];

    if (searchTerm) {
      const lowerCaseSearchTerm = searchTerm.toLowerCase();
      currentUsers = currentUsers.filter(
        (user) =>
          user.userName?.toLowerCase().includes(lowerCaseSearchTerm) ||
          user.userEmail?.toLowerCase().includes(lowerCaseSearchTerm)
      );
    }

    if (selectedUserType) {
      currentUsers = currentUsers.filter(
        (user) =>
          user.userType?.toLowerCase() === selectedUserType.toLowerCase()
      );
    }

    if (isAdminFilter !== "") {
      const filterBoolean = isAdminFilter === "true";
      currentUsers = currentUsers.filter(
        (user) => !!user.isAdmin === filterBoolean
      );
    }

    return currentUsers;
  }, [users, searchTerm, selectedUserType, isAdminFilter]);

  const handleUserSelect = (userId) => {
    setSelectedUserIds((prevSelected) => {
      const newSelected = new Set(prevSelected);
      if (newSelected.has(userId)) {
        newSelected.delete(userId);
      } else {
        newSelected.add(userId);
      }
      return newSelected;
    });
  };

  const handleSelectAllVisible = () => {
    setSelectedUserIds((prevSelected) => {
      const allVisibleUserIds = new Set(
        filteredUsers.map((user) => user.userId.toString())
      ); // Ensure string conversion
      // If all visible are already selected, deselect all. Otherwise, select all visible.
      const areAllVisibleSelected = filteredUsers.every((user) =>
        prevSelected.has(user.userId.toString())
      );

      if (areAllVisibleSelected) {
        // Deselect all visible
        const newSelected = new Set(prevSelected);
        allVisibleUserIds.forEach((id) => newSelected.delete(id));
        return newSelected;
      } else {
        // Select all visible
        return new Set([...prevSelected, ...allVisibleUserIds]);
      }
    });
  };

  // Check if all visible users are selected for the master checkbox
  const areAllVisibleSelected = useMemo(() => {
    if (filteredUsers.length === 0) return false;
    return filteredUsers.every((user) =>
      selectedUserIds.has(user.userId.toString())
    );
  }, [filteredUsers, selectedUserIds]);

  // const sendNotification = async () => {
  //   const selectedUsers = users.filter((user) =>
  //     selectedUserIds.has(user.userId.toString())
  //   );

  //   const pushMessages = [];

  //   selectedUsers.forEach((user) => {
  //     (user.pushTokens || []).forEach((tokenObj) => {
  //       if (tokenObj.expoPushToken) {
  //         pushMessages.push({
  //           to: tokenObj.expoPushToken,
  //           title: notificationTitle,
  //           body: notificationBody,
  //           sound: "default",
  //           data: {
  //             type: "custom", // you can customize this further if needed
  //             userId: user.userId,
  //           },
  //         });
  //       }
  //     });
  //   });

  //   if (pushMessages.length === 0) {
  //     alert("No push tokens found for selected users.");
  //     return;
  //   }

  //   try {
  //     const responses = await Promise.all(
  //       pushMessages.map((msg) =>
  //         fetch("https://exp.host/--/api/v2/push/send", {
  //           method: "POST",
  //           headers: {
  //             Accept: "application/json",
  //             "Accept-encoding": "gzip, deflate",
  //             "Content-Type": "application/json",
  //           },
  //           body: JSON.stringify(msg),
  //         })
  //       )
  //     );

  //     const results = await Promise.all(responses.map((res) => res.json()));
  //     console.log("Notification results:", results);
  //     alert("Notifications sent!");
  //     // Optionally clear notification state
  //     setNotificationTitle("");
  //     setNotificationBody("");
  //   } catch (error) {
  //     console.error("Error sending notifications:", error);
  //     alert("Failed to send notifications.");
  //   }
  // };

  const sendNotification = async () => {
    const selectedUsers = users.filter((user) =>
      selectedUserIds.has(user.userId.toString())
    );

    if (selectedUsers.length === 0) {
      alert("No users selected.");
      return;
    }

    const hasAnyTokens = selectedUsers.some(
      (user) => Array.isArray(user.pushTokens) && user.pushTokens.length > 0
    );

    if (!hasAnyTokens) {
      alert("No push tokens found for selected users.");
      return;
    }

    try {
      const res = await apiClient(routes.sendNotifications, {
        method: "POST",
        body: JSON.stringify({
          type: "custom_push",
          title: notificationTitle,
          body: notificationBody,
          users: selectedUsers.map((user) => ({
            userId: user.userId,
            pushTokens: user.pushTokens,
          })),
        }),
      });

      const data = await res.json();

      if (res.ok && data.success) {
        alert("Notifications sent successfully!");
        setNotificationTitle("");
        setNotificationBody("");
      } else {
        console.error(data);
        alert(
          data.message || "Some error occurred while sending notifications."
        );
      }
    } catch (error) {
      console.error("Error sending notifications:", error);
      alert("Failed to send notifications.");
    }
  };

  return (
    <div className="d-flex" style={{ minHeight: "100vh" }}>
      <Sidebar isCollapsed={isCollapsed} toggleSidebar={toggleSidebar} />
      <div
        className="p-4 w-100"
        style={{
          flexGrow: 1,
          transition: "margin-left 0.3s",
          marginLeft: isCollapsed
            ? `${sidebarWidthCollapsed}px`
            : `${sidebarWidthExpanded}px`,
        }}
      >
        <h2 className="mb-4 gilroy-bold">Notifications</h2>

        {/* --- Filter Section --- */}
        <div className="card mb-4 shadow-sm">
          <div className="card-body">
            <h5 className="card-title gilroy-bold mb-3">Filter Users</h5>
            <div className="row g-3">
              <div className="col-md-4">
                <label htmlFor="search" className="form-label gilroy-medium">
                  Search by Name or Email
                </label>
                <input
                  type="text"
                  className="form-control gilroy-medium"
                  id="search"
                  placeholder="Enter name or email..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="userTypeFilter"
                  className="form-label gilroy-medium"
                >
                  Filter by User Type
                </label>
                <select
                  className="form-select"
                  id="userTypeFilter"
                  value={selectedUserType}
                  onChange={(e) => setSelectedUserType(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All Types
                  </option>
                  <option value="actor" className="gilroy-medium">
                    Talent
                  </option>
                  <option value="casting" className="gilroy-medium">
                    Casting
                  </option>
                </select>
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="adminStatusFilter"
                  className="form-label gilroy-medium"
                >
                  Admin Status
                </label>
                <select
                  className="form-select"
                  id="adminStatusFilter"
                  value={isAdminFilter}
                  onChange={(e) => setIsAdminFilter(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All
                  </option>
                  <option value="true" className="gilroy-medium">
                    Admins
                  </option>
                  <option value="false" className="gilroy-medium">
                    Non-Admins
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        {/* --- End Filter Section --- */}

        {/* --- Send Notification Card --- */}
        {selectedUserIds.size > 0 && (
          <div className="card mb-4 shadow-sm">
            <div className="card-body">
              <h5 className="card-title gilroy-bold mb-3">
                Send Notification to {selectedUserIds.size} user
                {selectedUserIds.size > 1 ? "s" : ""}
              </h5>
              <div className="row g-3">
                <div className="col-md-6">
                  <label
                    htmlFor="notificationTitle"
                    className="form-label gilroy-medium"
                  >
                    Notification Title
                  </label>
                  <input
                    type="text"
                    className="form-control gilroy-medium"
                    id="notificationTitle"
                    placeholder="Enter title..."
                    value={notificationTitle}
                    onChange={(e) => setNotificationTitle(e.target.value)}
                  />
                </div>
                <div className="col-md-12">
                  <label
                    htmlFor="notificationBody"
                    className="form-label gilroy-medium"
                  >
                    Notification Body
                  </label>
                  <input
                    type="text"
                    className="form-control gilroy-medium"
                    id="notificationBody"
                    placeholder="Enter body..."
                    value={notificationBody}
                    onChange={(e) => setNotificationBody(e.target.value)}
                  />
                </div>
                <div className="col-12 text-end mt-3">
                  <button
                    className="btn btn-primary"
                    onClick={sendNotification}
                    disabled={!notificationTitle || !notificationBody}
                  >
                    Send Notification
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}

        {loading ? (
          <div className="text-center mt-5">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">Loading users...</span>
            </div>
            <p className="mt-2">Loading users...</p>
          </div>
        ) : users.length === 0 ? (
          <div className="alert alert-info mt-4" role="alert">
            No users found.
          </div>
        ) : (
          <div className="table-responsive">
            <table className="table table-hover table-striped gilroy-medium">
              <thead className="table-light">
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Type</th>
                  <th scope="col">Admin</th>
                  <th scope="col">
                    <div className="form-check">
                      <input
                        className="form-check-input"
                        type="checkbox"
                        id="select-all-visible"
                        checked={areAllVisibleSelected}
                        onChange={handleSelectAllVisible}
                      />
                      <label
                        className="form-check-label"
                        htmlFor="select-all-visible"
                        style={{ fontWeight: "normal" }}
                      >
                        Select
                      </label>
                    </div>
                  </th>
                </tr>
              </thead>
              <tbody>
                {filteredUsers.length === 0 ? (
                  <tr>
                    <td colSpan="6" className="text-center py-4">
                      {" "}
                      {/* Adjusted colSpan */}
                      No users match your current filters.
                    </td>
                  </tr>
                ) : (
                  filteredUsers.map((user, index) => (
                    <tr key={user._id}>
                      <th scope="row">{index + 1}</th>
                      <td>
                        <button
                          className="btn btn-link p-0 text-decoration-none"
                          onClick={() => navigate(`/users/${user.userId}`)}
                          style={{ color: "#007bff", fontWeight: "bold" }}
                        >
                          {user.userName}
                        </button>
                      </td>
                      <td>{user.userEmail}</td>
                      <td>
                        <span
                          className={`badge ${getRoleBadgeClass(user.role)}`}
                        >
                          {getDisplayRole(user.userType)}
                        </span>
                      </td>
                      <td>
                        {user.isAdmin ? (
                          <span className="badge bg-success">Yes</span>
                        ) : (
                          <span className="badge bg-secondary">No</span>
                        )}
                      </td>
                      <td>
                        {/* Individual User Checkbox */}
                        <div className="form-check">
                          <input
                            className="form-check-input"
                            type="checkbox"
                            value={user.userId}
                            id={`user-${user.userId}`}
                            checked={selectedUserIds.has(
                              user.userId.toString()
                            )} // Ensure string conversion
                            onChange={() =>
                              handleUserSelect(user.userId.toString())
                            } // Ensure string conversion
                          />
                          <label
                            className="form-check-label visually-hidden"
                            htmlFor={`user-${user.userId}`}
                          >
                            Select {user.userName}
                          </label>
                        </div>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default Notifications;
