import React, { useState } from "react";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { loginUser } from "../services/authService"; // Adjust path as needed
import "./Login.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      await loginUser(email, password, dispatch);
      navigate("/dashboard");
    } catch (error) {
      alert(error.message);
    }
  };

  return (
    <div className="d-flex align-items-center justify-content-center vh-100 bg-light">
      <form
        onSubmit={handleLogin}
        className="p-4 shadow rounded bg-white"
        style={{ width: "100%", maxWidth: 400 }}
      >
        <h3 className="text-center gilroy-semi-bold">Admin Login</h3>
        <div className="mb-3">
          <label htmlFor="email" className="form-label gilroy-medium">
            Email address
          </label>
          <input
            type="email"
            className="form-control gilroy-medium"
            id="email"
            placeholder="admin@example.com"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
        </div>
        <div className="mb-4">
          <label htmlFor="password" className="form-label gilroy-medium">
            Password
          </label>
          <input
            type="password"
            className="form-control gilroy-medium"
            id="password"
            placeholder="********"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
        </div>
        <button type="submit" className="w-100 custom-login-button">
          Log In
        </button>
      </form>
    </div>
  );
};

export default Login;
