// src/pages/Users.jsx (Updated)
import React, { useEffect, useState, useMemo } from "react";
import Sidebar from "../components/Sidebar";
import apiClient from "../api/apiClient";
import { useNavigate } from "react-router-dom";
import { Lock, Unlock, CheckCircle, XCircle } from "lucide-react"; // Import icons, added CheckCircle/XCircle
import routes from "../network/routes";
import AdminConfirmationModal from "../components/AdminConfirmationModal";
import VerificationModal from "../components/VerificationModal"; // Import the updated VerificationModal

const ADMIN_PASSWORD = "supersecretadminpassword";

const Users = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [searchTerm, setSearchTerm] = useState("");
  const [selectedUserType, setSelectedUserType] = useState("");
  const [isAdminFilter, setIsAdminFilter] = useState("");

  // States for Admin Confirmation Modal
  const [showPasswordModal, setShowPasswordModal] = useState(false);
  const [modalPassword, setModalPassword] = useState(""); // Common password state for both modals
  const [modalError, setModalError] = useState(""); // Common error state for both modals

  // States for targeted user in either modal
  const [targetUserId, setTargetUserId] = useState(null);
  const [targetIsAdminStatus, setTargetIsAdminStatus] = useState(false); // For admin toggle

  // States for Verification Modal specific data
  const [showVerificationModal, setShowVerificationModal] = useState(false);
  const [galleryImage, setGalleryImage] = useState("");
  const [cameraImage, setCameraImage] = useState("");
  const [verificationStatus, setVerificationStatus] = useState("");

  const navigate = useNavigate();

  const toggleSidebar = () => setIsCollapsed((prev) => !prev);

  // --- Fetch Users ---
  const fetchUsers = async () => {
    setLoading(true);
    try {
      const res = await apiClient(routes.user);
      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || "Failed to fetch users");
      }
      const data = await res.json();
      console.log("Fetched users:", data.data.data);
      setUsers(data.data.data);
    } catch (error) {
      console.error("Error fetching users:", error.message);
      alert(`Failed to fetch users: ${error.message}`); // Inform user
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, []);

  const getDisplayRole = (role) => {
    switch (role?.toLowerCase()) {
      case "actor":
        return "Talent";
      case "casting":
        return "Casting";
      default:
        return "N/A";
    }
  };

  const getRoleBadgeClass = (role) => {
    switch (role?.toLowerCase()) {
      case "actor":
        return "bg-warning text-dark";
      case "casting":
        return "bg-info text-dark";
      default:
        return "bg-secondary text-white";
    }
  };

  // Define sidebar widths to use for margin
  const sidebarWidthCollapsed = 80;
  const sidebarWidthExpanded = 250;

  // --- Filtering Logic ---
  const filteredUsers = useMemo(() => {
    let currentUsers = [...users];

    if (searchTerm) {
      const lowerCaseSearchSearchTerm = searchTerm.toLowerCase();
      currentUsers = currentUsers.filter(
        (user) =>
          user.firstName?.toLowerCase().includes(lowerCaseSearchSearchTerm) ||
          user.lastName?.toLowerCase().includes(lowerCaseSearchSearchTerm) ||
          user.email?.toLowerCase().includes(lowerCaseSearchSearchTerm)
      );
    }

    if (selectedUserType) {
      currentUsers = currentUsers.filter(
        (user) => user.role?.toLowerCase() === selectedUserType.toLowerCase()
      );
    }

    if (isAdminFilter !== "") {
      const filterBoolean = isAdminFilter === "true";
      currentUsers = currentUsers.filter(
        (user) => !!user.isAdmin === filterBoolean
      );
    }

    return currentUsers;
  }, [users, searchTerm, selectedUserType, isAdminFilter]);

  // --- Admin Toggle Handlers ---
  const handleToggleAdminClick = (userId, currentIsAdmin) => {
    setTargetUserId(userId);
    setTargetIsAdminStatus(!currentIsAdmin); // Set to the *new* desired status
    setModalPassword(""); // Clear previous password
    setModalError(""); // Clear previous error
    setShowPasswordModal(true); // Show the admin confirmation modal
  };

  const confirmAdminToggle = async () => {
    if (modalPassword !== ADMIN_PASSWORD) {
      setModalError("Incorrect password.");
      return;
    }

    setModalError(""); // Clear error on success attempt
    setShowPasswordModal(false); // Hide the modal immediately

    try {
      const res = await apiClient(routes.updateMe, {
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          _id: targetUserId,
          isAdmin: targetIsAdminStatus,
        }),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || "Failed to update admin status");
      }

      fetchUsers(); // Re-fetch users to reflect the change
      alert(
        `User's admin status updated to ${
          targetIsAdminStatus ? "Admin" : "Non-Admin"
        } successfully!`
      );
    } catch (error) {
      console.error("Error updating admin status:", error);
      alert(`Failed to update admin status: ${error.message}`);
    } finally {
      setTargetUserId(null); // Reset target user
      setTargetIsAdminStatus(false); // Reset target status
    }
  };

  // --- Verification Handlers ---
  const handleToggleVerificationClick = async (userId) => {
    setTargetUserId(userId);
    setModalPassword(""); // Clear password
    setModalError(""); // Clear error
    setGalleryImage(""); // Clear previous data
    setCameraImage("");
    setVerificationStatus("");

    // Fetch verification data first
    try {
      const res = await apiClient(`${routes.verification}?userId=${userId}`, {
        method: "GET",
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(
          errorData.message || "Failed to fetch verification data"
        );
      }

      const responseData = await res.json();
      console.log("Verification response:", responseData.data.verification);
      const verification = responseData.data.verification;

      if (verification) {
        setGalleryImage(verification.galleryImage || "");
        setCameraImage(verification.cameraImage || "");
        setVerificationStatus(
          verification.verificationStatus || "Not Submitted"
        );
      } else {
        setVerificationStatus("No Data Submitted Yet");
      }
    } catch (error) {
      console.error("Error fetching verification data:", error);
      setModalError(`Failed to load verification data: ${error.message}`);
      // Optionally, don't open modal or show alert to user immediately
    } finally {
      setShowVerificationModal(true); // Always show modal, even if no data, to display status/error
    }
  };

  const confirmVerification = async (status) => {
    // 'status' will be 'verified' or 'rejected'
    if (modalPassword !== ADMIN_PASSWORD) {
      setModalError("Incorrect password.");
      return;
    }

    setModalError(""); // Clear error on success attempt
    setShowVerificationModal(false); // Hide the modal immediately

    try {
      const res = await apiClient(routes.verification + "Status", {
        // Assuming verification route for PATCH
        method: "PATCH",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({
          userId: targetUserId,
          verificationStatus: status, // Send the desired status: 'verified' or 'rejected'
        }),
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.message || `Failed to ${status} user`);
      }

      fetchUsers(); // Re-fetch users to reflect the change
      alert(`User verification status updated to '${status}' successfully!`);
    } catch (error) {
      console.error(`Error updating verification status to ${status}:`, error);
      alert(`Failed to update verification status: ${error.message}`);
    } finally {
      setTargetUserId(null); // Reset target user
      setGalleryImage(""); // Clear images
      setCameraImage("");
      setVerificationStatus(""); // Clear status
    }
  };

  return (
    <div className="d-flex" style={{ minHeight: "100vh" }}>
      <Sidebar isCollapsed={isCollapsed} toggleSidebar={toggleSidebar} />
      <div
        className="p-4 w-100"
        style={{
          flexGrow: 1,
          transition: "margin-left 0.3s",
          marginLeft: isCollapsed
            ? `${sidebarWidthCollapsed}px`
            : `${sidebarWidthExpanded}px`,
        }}
      >
        <h2 className="mb-4 gilroy-bold">Users</h2>

        {/* --- Filter Section --- */}
        <div className="card mb-4 shadow-sm">
          <div className="card-body">
            <h5 className="card-title gilroy-bold mb-3">Filter Users</h5>
            <div className="row g-3">
              <div className="col-md-4">
                <label htmlFor="search" className="form-label gilroy-medium">
                  Search by Name or Email
                </label>
                <input
                  type="text"
                  className="form-control gilroy-medium"
                  id="search"
                  placeholder="Enter name or email..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                />
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="userTypeFilter"
                  className="form-label gilroy-medium"
                >
                  Filter by User Type
                </label>
                <select
                  className="form-select gilroy-medium"
                  id="userTypeFilter"
                  value={selectedUserType}
                  onChange={(e) => setSelectedUserType(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All Types
                  </option>
                  <option value="actor" className="gilroy-medium">
                    Talent
                  </option>
                  <option value="casting" className="gilroy-medium">
                    Casting
                  </option>
                </select>
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="adminStatusFilter"
                  className="form-label gilroy-medium"
                >
                  Admin Status
                </label>
                <select
                  className="form-select gilroy-medium"
                  id="adminStatusFilter"
                  value={isAdminFilter}
                  onChange={(e) => setIsAdminFilter(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All
                  </option>
                  <option value="true" className="gilroy-medium">
                    Admins
                  </option>
                  <option value="false" className="gilroy-medium">
                    Non-Admins
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        {/* --- End Filter Section --- */}

        {loading ? (
          <div className="text-center mt-5">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">Loading users...</span>
            </div>
            <p className="mt-2">Loading users...</p>
          </div>
        ) : users.length === 0 ? (
          <div className="alert alert-info mt-4" role="alert">
            No users found.
          </div>
        ) : (
          <div className="table-responsive">
            <table className="table table-hover table-striped gilroy-medium">
              <thead className="table-light">
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Name</th>
                  <th scope="col">Email</th>
                  <th scope="col">Type</th>
                  <th scope="col">Verification</th>
                  <th scope="col">Admin Actions</th>
                </tr>
              </thead>
              <tbody>
                {filteredUsers.length === 0 ? (
                  <tr>
                    <td colSpan="6" className="text-center py-4">
                      No users match your current filters.
                    </td>
                  </tr>
                ) : (
                  filteredUsers.map((user, index) => (
                    <tr key={user._id}>
                      <th scope="row">{index + 1}</th>
                      <td>
                        <button
                          className="btn btn-link p-0 text-decoration-none"
                          onClick={() => navigate(`/users/${user._id}`)}
                          style={{ color: "#007bff", fontWeight: "bold" }}
                        >
                          {user.firstName} {user.lastName}
                        </button>
                      </td>
                      <td>{user.email}</td>
                      <td>
                        <span
                          className={`badge ${getRoleBadgeClass(user.role)}`}
                        >
                          {getDisplayRole(user.role)}
                        </span>
                      </td>
                      <td>
                        {/* Verification Toggle Button */}
                        <button
                          className={`btn btn-sm ${
                            user.verified == "verified"
                              ? "btn-outline-success"
                              : "btn-outline-danger"
                          }`}
                          onClick={() =>
                            handleToggleVerificationClick(user._id)
                          }
                        >
                          {user.verified == "verified" ? (
                            <CheckCircle size={16} /> // Icon for verified
                          ) : (
                            <XCircle size={16} /> // Icon for pending/not verified
                          )}
                          <span className="ms-2 d-none d-md-inline-block">
                            {user.verified}
                          </span>
                        </button>
                      </td>
                      <td>
                        {/* Admin Toggle Button */}
                        <button
                          className={`btn btn-sm ${
                            user.isAdmin
                              ? "btn-outline-danger"
                              : "btn-outline-success"
                          }`}
                          onClick={() =>
                            handleToggleAdminClick(user._id, user.isAdmin)
                          }
                          title={user.isAdmin ? "Remove Admin" : "Make Admin"}
                        >
                          {user.isAdmin ? (
                            <Lock size={16} /> // Locked icon for admin
                          ) : (
                            <Unlock size={16} /> // Unlocked icon for non-admin
                          )}
                          <span className="ms-2 d-none d-md-inline-block">
                            {user.isAdmin ? "Remove" : "Make Admin"}
                          </span>
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>
        )}
      </div>

      {/* Admin Confirmation Modal */}
      <AdminConfirmationModal
        show={showPasswordModal}
        onClose={() => setShowPasswordModal(false)}
        onConfirm={confirmAdminToggle}
        password={modalPassword}
        onPasswordChange={(e) => setModalPassword(e.target.value)}
        error={modalError}
        targetIsAdminStatus={targetIsAdminStatus}
      />

      {/* Verification Modal */}
      <VerificationModal
        show={showVerificationModal}
        onClose={() => setShowVerificationModal(false)}
        onApprove={() => confirmVerification("verified")} // Pass 'verified' status
        onReject={() => confirmVerification("rejected")} // Pass 'rejected' status
        password={modalPassword}
        onPasswordChange={(e) => {
          setModalPassword(e.target.value);
          setModalError(""); // Clear error when typing
        }}
        error={modalError}
        galleryImage={galleryImage}
        cameraImage={cameraImage}
        verificationStatus={verificationStatus}
      />
    </div>
  );
};

export default Users;
