import React, { useState, useEffect, useMemo } from "react"; // Import useMemo
import Sidebar from "../components/Sidebar";
import apiClient from "../api/apiClient";
import { useNavigate } from "react-router-dom";
import routes from "../network/routes";

const AllGigs = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [gigs, setGigs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const navigate = useNavigate();

  // --- New Filter States ---
  const [companyFilter, setCompanyFilter] = useState("");
  const [statusFilter, setStatusFilter] = useState(""); // 'active', 'expired', or ''
  const [paidFilter, setPaidFilter] = useState(""); // 'true', 'false', or ''
  // --- End New Filter States ---

  const toggleSidebar = () => {
    setIsCollapsed((prev) => !prev);
  };

  const sidebarWidthCollapsed = 80;
  const sidebarWidthExpanded = 250;

  useEffect(() => {
    const fetchGigs = async () => {
      setLoading(true);
      setError(null);
      try {
        const res = await apiClient(routes.gig);
        if (!res.ok) {
          const errorData = await res.json();
          throw new Error(errorData.message || "Failed to fetch gigs");
        }
        const data = await res.json();
        setGigs(data.data.gigs || []);
        console.log("Fetched gigs:", data.data.gigs);
      } catch (err) {
        console.error("Error fetching gigs:", err);
        setError(err.message || "An error occurred while fetching gigs.");
      } finally {
        setLoading(false);
      }
    };

    fetchGigs();
  }, []);

  const isGigActive = (expirationDate) => {
    if (!expirationDate) {
      return false;
    }
    const now = new Date();
    const expiry = new Date(expirationDate);
    return expiry > now;
  };

  // --- Filtering Logic for Gigs ---
  const filteredGigs = useMemo(() => {
    let currentGigs = [...gigs];

    // 1. Filter by Company Name
    if (companyFilter) {
      const lowerCaseCompanyFilter = companyFilter.toLowerCase();
      currentGigs = currentGigs.filter((gig) =>
        gig.companyName?.toLowerCase().includes(lowerCaseCompanyFilter)
      );
    }

    // 2. Filter by Status (Active/Expired)
    if (statusFilter) {
      currentGigs = currentGigs.filter((gig) => {
        const activeStatus = isGigActive(gig.expirationData);
        if (statusFilter === "active") {
          return activeStatus;
        }
        if (statusFilter === "expired") {
          return !activeStatus;
        }
        return true; // Should not reach here if statusFilter is 'active' or 'expired'
      });
    }

    // 3. Filter by Paid Status
    if (paidFilter !== "") {
      // Check for explicit 'true' or 'false' string
      const isPaidBoolean = paidFilter === "true";
      currentGigs = currentGigs.filter(
        (gig) => !!gig.isPaid === isPaidBoolean // Ensure gig.isPaid is treated as boolean
      );
    }

    return currentGigs;
  }, [gigs, companyFilter, statusFilter, paidFilter]); // Re-run when these dependencies change
  // --- End Filtering Logic ---

  return (
    <div className="d-flex" style={{ minHeight: "100vh" }}>
      <Sidebar isCollapsed={isCollapsed} toggleSidebar={toggleSidebar} />
      <div
        className="p-4 w-100"
        style={{
          flexGrow: 1,
          transition: "margin-left 0.3s",
          marginLeft: isCollapsed
            ? `${sidebarWidthCollapsed}px`
            : `${sidebarWidthExpanded}px`,
        }}
      >
        <h2 className="mb-4 gilroy-bold">All Gigs</h2>

        {/* --- Filter Section --- */}
        <div className="card mb-4 shadow-sm">
          <div className="card-body">
            <h5 className="card-title gilroy-bold mb-3">Filter Gigs</h5>
            <div className="row g-3">
              <div className="col-md-4">
                <label
                  htmlFor="companyFilter"
                  className="form-label gilroy-medium"
                >
                  Filter by Company
                </label>
                <input
                  type="text"
                  className="form-control gilroy-medium"
                  id="companyFilter"
                  placeholder="Enter company name..."
                  value={companyFilter}
                  onChange={(e) => setCompanyFilter(e.target.value)}
                />
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="statusFilter"
                  className="form-label gilroy-medium"
                >
                  Filter by Status
                </label>
                <select
                  className="form-select"
                  id="statusFilter"
                  value={statusFilter}
                  onChange={(e) => setStatusFilter(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All Statuses
                  </option>
                  <option value="active" className="gilroy-medium">
                    Active
                  </option>
                  <option value="expired" className="gilroy-medium">
                    Expired
                  </option>
                </select>
              </div>
              <div className="col-md-4">
                <label
                  htmlFor="paidFilter"
                  className="form-label gilroy-medium"
                >
                  Filter by Payment
                </label>
                <select
                  className="form-select"
                  id="paidFilter"
                  value={paidFilter}
                  onChange={(e) => setPaidFilter(e.target.value)}
                >
                  <option value="" className="gilroy-medium">
                    All
                  </option>
                  <option value="true" className="gilroy-medium">
                    Paid Gigs
                  </option>
                  <option value="false" className="gilroy-medium">
                    Unpaid Gigs
                  </option>
                </select>
              </div>
            </div>
          </div>
        </div>
        {/* --- End Filter Section --- */}

        {loading ? (
          <div className="text-center mt-5">
            <div className="spinner-border text-primary" role="status">
              <span className="visually-hidden">Loading gigs...</span>
            </div>
            <p className="mt-2">Loading gigs...</p>
          </div>
        ) : error ? (
          <div className="alert alert-danger mt-4" role="alert">
            Error: {error}
          </div>
        ) : filteredGigs.length === 0 &&
          (companyFilter || statusFilter || paidFilter) ? (
          <div className="alert alert-info mt-4" role="alert">
            No gigs match your current filters.
          </div>
        ) : gigs.length === 0 ? (
          <div className="alert alert-info mt-4" role="alert">
            No gigs found.
          </div>
        ) : (
          <div className="table-responsive">
            <table className="table table-hover table-striped gilroy-medium">
              <thead className="table-light">
                <tr>
                  <th scope="col">#</th>
                  <th scope="col">Title</th>
                  <th scope="col">Company</th>
                  <th scope="col">Roles</th>
                  <th scope="col">Location</th>
                  <th scope="col">Status</th>
                  <th scope="col">Paid</th>
                  <th scope="col">Expires</th>
                </tr>
              </thead>
              <tbody>
                {filteredGigs.map((gig, index) => (
                  <tr key={gig._id}>
                    <th scope="row">{index + 1}</th>
                    <td>
                      <button
                        className="btn btn-link p-0 text-decoration-none"
                        onClick={() => {
                          localStorage.setItem(
                            "selectedGig",
                            JSON.stringify(gig)
                          );
                          navigate(`/gigs`);
                        }}
                        style={{ color: "#007bff", fontWeight: "bold" }}
                      >
                        {gig.projectTitle}
                      </button>
                    </td>
                    <td>{gig.companyName || "N/A"}</td>
                    <td>{gig.roles?.length || "N/A"}</td>{" "}
                    {/* Added optional chaining */}
                    <td>
                      {gig.locations && gig.locations.length > 0
                        ? gig.locations
                            .map((loc) => `${loc.city}, ${loc.stateCode}`)
                            .join(", ")
                        : "Anywhere"}
                    </td>
                    <td>
                      {isGigActive(gig.expirationData) ? (
                        <span className="badge bg-success">Active</span>
                      ) : (
                        <span className="badge bg-danger">Expired</span>
                      )}
                    </td>
                    <td>
                      {gig.isPaid ? (
                        <span className="badge bg-success">Yes</span>
                      ) : (
                        <span className="badge bg-warning text-dark">No</span>
                      )}
                    </td>
                    <td>
                      {gig.expirationData
                        ? new Date(gig.expirationData).toLocaleDateString(
                            "en-GB",
                            {
                              day: "2-digit",
                              month: "short",
                              year: "numeric",
                            }
                          )
                        : "N/A"}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
};

export default AllGigs;
