import React, { useState, useEffect } from "react";
import Sidebar from "../components/Sidebar";
import apiClient from "../api/apiClient"; // Assuming you have this for fetching data
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
  ResponsiveContainer,
} from "recharts"; // Import Recharts components
import routes from "../network/routes";

const Dashboard = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [userGrowthData, setUserGrowthData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const toggleSidebar = () => {
    setIsCollapsed((prev) => !prev);
  };

  useEffect(() => {
    const fetchAndProcessUserGrowth = async () => {
      try {
        const res = await apiClient(routes.user);
        if (!res.ok) {
          const errorData = await res.json();
          throw new Error(
            errorData.message || "Failed to fetch users for analytics"
          );
        }
        const data = await res.json();
        const users = data.data.data; // Assuming this path to your user array

        // --- Data Processing for Chart ---
        const monthlyCounts = {};

        users.forEach((user) => {
          if (user.createdAt) {
            const date = new Date(user.createdAt);
            // Get year and month (0-11)
            const year = date.getFullYear();
            const month = date.getMonth(); // 0 for Jan, 11 for Dec

            // Create a key like "YYYY-MM" to group by month
            const monthKey = `${year}-${String(month + 1).padStart(2, "0")}`; // Pad month with 0 for sorting

            if (!monthlyCounts[monthKey]) {
              monthlyCounts[monthKey] = 0;
            }
            monthlyCounts[monthKey]++;
          }
        });

        // Convert the aggregated data into an array suitable for Recharts
        const chartData = Object.keys(monthlyCounts)
          .sort() // Sorts by monthKey (YYYY-MM)
          .map((monthKey) => {
            const [year, monthNum] = monthKey.split("-");
            const dateObj = new Date(year, parseInt(monthNum) - 1, 1); // Create a date object for formatting
            const monthName = dateObj.toLocaleString("en-US", {
              month: "short",
              year: "2-digit",
            }); // e.g., "Jan '25"
            return {
              name: monthName, // Label for X-axis (e.g., "Jan 2025")
              users: monthlyCounts[monthKey], // Count of users for that month
            };
          });

        setUserGrowthData(chartData);
        // --- End Data Processing ---
      } catch (err) {
        console.error("Error fetching user growth data:", err);
        setError("Failed to load user growth data.");
      } finally {
        setLoading(false);
      }
    };

    fetchAndProcessUserGrowth();
  }, []); // Empty dependency array means this runs once on mount

  // Define sidebar widths to use for margin
  const sidebarWidthCollapsed = 80; // Corresponds to Sidebar's width
  const sidebarWidthExpanded = 250; // Corresponds to Sidebar's width

  return (
    <div className="d-flex" style={{ minHeight: "100vh" }}>
      <Sidebar isCollapsed={isCollapsed} toggleSidebar={toggleSidebar} />
      <div
        className="p-4"
        style={{
          flexGrow: 1,
          transition: "margin-left 0.3s",
          marginLeft: isCollapsed
            ? `${sidebarWidthCollapsed}px`
            : `${sidebarWidthExpanded}px`,
        }}
      >
        <h2 className="mb-4 gilroy-bold">Dashboard</h2>

        <div className="row">
          <div className="col-lg-8 col-md-10">
            {" "}
            {/* Chart takes up 8 or 10 columns */}
            <div className="card shadow-sm mb-4">
              <div className="card-header gilroy-bold">
                Users Joined Per Month
              </div>
              <div className="card-body">
                {loading ? (
                  <div className="text-center py-5">
                    <div className="spinner-border text-primary" role="status">
                      <span className="visually-hidden">Loading chart...</span>
                    </div>
                    <p className="mt-2">Loading chart data...</p>
                  </div>
                ) : error ? (
                  <div className="alert alert-danger" role="alert">
                    {error}
                  </div>
                ) : userGrowthData.length === 0 ? (
                  <div className="alert alert-info" role="alert">
                    No user data available to generate chart.
                  </div>
                ) : (
                  <ResponsiveContainer width="100%" height={400}>
                    <LineChart
                      data={userGrowthData}
                      margin={{ top: 20, right: 40, left: 0, bottom: 20 }}
                    >
                      <defs>
                        <linearGradient
                          id="colorUsers"
                          x1="0"
                          y1="0"
                          x2="0"
                          y2="1"
                        >
                          <stop
                            offset="5%"
                            stopColor="#8884d8"
                            stopOpacity={0.8}
                          />
                          <stop
                            offset="95%"
                            stopColor="#8884d8"
                            stopOpacity={0}
                          />
                        </linearGradient>
                      </defs>

                      <CartesianGrid stroke="#e0e0e0" strokeDasharray="4 4" />

                      <XAxis
                        dataKey="name"
                        tick={{ fontSize: 12, fill: "#555" }}
                        axisLine={{ stroke: "#ccc" }}
                        tickLine={{ stroke: "#ccc" }}
                      />

                      <YAxis
                        allowDecimals={false}
                        tick={{ fontSize: 12, fill: "#555" }}
                        axisLine={{ stroke: "#ccc" }}
                        tickLine={{ stroke: "#ccc" }}
                        label={{
                          value: "Users",
                          angle: -90,
                          position: "insideLeft",
                          style: { textAnchor: "middle", fill: "#888" },
                        }}
                      />

                      <Tooltip
                        contentStyle={{
                          backgroundColor: "#fff",
                          border: "1px solid #ccc",
                          borderRadius: 8,
                          fontSize: 14,
                        }}
                        labelStyle={{ fontWeight: "bold" }}
                        cursor={{
                          stroke: "#8884d8",
                          strokeWidth: 1,
                          opacity: 0.2,
                        }}
                      />

                      <Legend
                        verticalAlign="top"
                        height={36}
                        iconType="circle"
                        wrapperStyle={{ fontSize: 14, color: "#666" }}
                      />

                      <Line
                        type="monotone"
                        dataKey="users"
                        stroke="#6C63FF"
                        strokeWidth={3}
                        dot={{
                          r: 4,
                          strokeWidth: 2,
                          fill: "#fff",
                          stroke: "#6C63FF",
                        }}
                        activeDot={{
                          r: 6,
                          stroke: "#6C63FF",
                          strokeWidth: 2,
                          fill: "#fff",
                        }}
                        name="New Users"
                      />
                    </LineChart>
                  </ResponsiveContainer>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* You can add more dashboard widgets/cards here */}
      </div>
    </div>
  );
};

export default Dashboard;
