import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import apiClient from "../api/apiClient";
import Sidebar from "../components/Sidebar";
import "./UserProfile.css";
// Import icons for better visual representation
import { Link as LinkIcon, Youtube, Image as ImageIcon } from "lucide-react";
import routes from "../network/routes";

// Helper function to safely parse social links string
const parseSocialLinks = (socialArray) => {
  if (!socialArray || !Array.isArray(socialArray)) {
    return [];
  }
  return socialArray
    .map((linkString) => {
      try {
        const parsedLink = JSON.parse(linkString);
        // Ensure the URL has a protocol (http:// or https://)
        // If it doesn't, default to 'https://'
        if (
          parsedLink &&
          parsedLink.url &&
          !/^https?:\/\//i.test(parsedLink.url)
        ) {
          parsedLink.url = `https://${parsedLink.url}`;
        }
        return parsedLink;
      } catch (e) {
        console.error("Error parsing social link string:", linkString, e);
        return null; // Return null for parsing errors
      }
    })
    .filter((link) => link !== null); // Filter out any null entries
};

// Helper function to extract YouTube video ID from URL
const getYouTubeVideoId = (url) => {
  // Regex to match YouTube video IDs from various URL formats
  const regExp =
    /(?:https?:\/\/)?(?:www\.)?(?:m\.)?(?:youtube\.com|youtu\.be)\/(?:watch\?v=|embed\/|v\/|)([\w-]{11})(?:\S+)?/;
  const match = url.match(regExp);
  return match && match[1] ? match[1] : null;
};

const UserProfile = () => {
  const { id } = useParams();
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // Define sidebar widths to use for margin
  const sidebarWidthCollapsed = 80; // Corresponds to Sidebar's width
  const sidebarWidthExpanded = 250; // Corresponds to Sidebar's width

  useEffect(() => {
    const fetchUser = async () => {
      try {
        const res = await apiClient(routes.user + `/${id}`, {
          method: "GET",
        });
        if (!res.ok) {
          const errorData = await res.json();
          throw new Error(errorData.message || "Failed to fetch user");
        }
        const data = await res.json();
        setUser(data.user);
        console.log(data.user);
      } catch (error) {
        console.error("Error fetching user:", error.message);
      } finally {
        setLoading(false);
      }
    };

    fetchUser();
  }, [id]);

  if (loading) {
    return (
      <div className="p-4 text-center">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Loading profile...</span>
        </div>
        <p className="mt-2">Loading profile...</p>
      </div>
    );
  }

  if (!user) {
    return (
      <div className="p-4 alert alert-warning" role="alert">
        User not found.
      </div>
    );
  }

  // Parse social links here after user data is fetched
  const parsedSocials = parseSocialLinks(user.social);
  const hasImages =
    user.media && user.media.images && user.media.images.length > 0;
  const hasVideos =
    user.media && user.media.videos && user.media.videos.length > 0;

  return (
    <div className="d-flex" style={{ minHeight: "100vh" }}>
      {/* Changed to minHeight */}
      <Sidebar
        isCollapsed={isCollapsed}
        toggleSidebar={() => setIsCollapsed(!isCollapsed)}
      />
      <div
        className="p-4 w-100"
        style={{
          flexGrow: 1,
          transition: "margin-left 0.3s",
          marginLeft: isCollapsed
            ? `${sidebarWidthCollapsed}px`
            : `${sidebarWidthExpanded}px`,
        }}
      >
        {/* Profile Header */}
        <h2 className="mb-4 gilroy-bold">User Profile</h2>
        <div className="user-profile-content">
          {/* Added a wrapper div for content sections */}
          {user.image && (
            <img
              src={user.profileImageUrl}
              alt={`${user.firstName} ${user.lastName}`}
              className="img-fluid rounded mb-4 shadow-sm" // Added Bootstrap classes for better styling
              style={{
                maxWidth: "300px", // Limit max width
                height: "auto", // Maintain aspect ratio
                objectFit: "cover",
              }}
            />
          )}
          <h4 className="mb-3 gilroy-semi-bold">
            {user.firstName} {user.lastName}
          </h4>
          <p className="mb-0 info-text">
            <span className="info-text-title">EMAIL:</span> {user.email}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">PHONE:</span> {user.phone}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">ROLE:</span> {user.role}
          </p>
          <hr className="my-4" /> {/* Added margin to hr */}
          {/* Basic Info */}
          <h4 className="mb-2 gilroy-medium">Basic Info</h4>
          <p className="mb-0 info-text" style={{ margin: 0 }}>
            {user.interests && user.interests.length > 0
              ? user.interests.map(
                  (item, index) =>
                    `${item}${index !== user.interests.length - 1 ? ". " : "."}`
                )
              : "None"}
          </p>
          <p className="mb-0 info-text">
            {user.city
              ? `${user.city}, ${user.state}`
              : "Location not specified"}
          </p>
          <hr className="my-4" />
          {/* About Me */}
          <h4 className="mb-2 gilroy-medium">About Me</h4>
          <p className="mb-0 info-text">
            {user.bio || "No biography provided."}
          </p>
          <hr className="my-4" />
          {/* Appearance */}
          <h4 className="mb-2 gilroy-medium">Appearance</h4>
          <p className="mb-0 info-text">
            <span className="info-text-title">AGE RANGE:</span>{" "}
            {user?.appearance?.ageMin
              ? `${user.appearance.ageMin}-${user.appearance.ageMax}`
              : "N/A"}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">GENDER:</span>{" "}
            {user?.appearance?.gender || "N/A"}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">HEIGHT:</span>{" "}
            {user?.appearance?.height
              ? (() => {
                  const cm = user.appearance.height;
                  const totalInches = Math.round(cm / 2.54);
                  const feet = Math.floor(totalInches / 12);
                  const inches = totalInches % 12;
                  return `${feet} ft, ${inches} in`;
                })()
              : "N/A"}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">WEIGHT:</span>{" "}
            {user?.appearance?.weight ? `${user.appearance.weight} kg` : "N/A"}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">BODY TYPE:</span>{" "}
            {user?.appearance?.bodyType || "N/A"}
          </p>
          <p className="mb-0 info-text">
            <span className="info-text-title">ETHNICITY:</span>{" "}
            {user?.appearance?.ethnicity || "N/A"}
          </p>
          <hr className="my-4" />
          {/* Skills */}
          <h4 className="mb-2 gilroy-medium">Skills</h4>
          {user.skills && user.skills.length > 0 ? (
            <ul className="info-text list-unstyled">
              {" "}
              {/* list-unstyled for no default list styling */}
              {user.skills.map((skill, index) => (
                <li key={index} className="mb-1">
                  <span className="info-text-title me-2">
                    {String(index + 1).padStart(2, "0")}.
                  </span>{" "}
                  {skill}
                </li>
              ))}
            </ul>
          ) : (
            <p className="info-text">No skills listed.</p>
          )}
          <hr className="my-4" />
          {/* Languages */}
          <h4 className="mb-2 gilroy-medium">Languages</h4>
          {user.languages && user.languages.length > 0 ? (
            <ul className="info-text list-unstyled">
              {user.languages.map((language, index) => (
                <li key={index} className="mb-1">
                  <span className="info-text-title me-2">
                    {String(index + 1).padStart(2, "0")}.
                  </span>{" "}
                  {language}
                </li>
              ))}
            </ul>
          ) : (
            <p className="info-text">No languages listed.</p>
          )}
          <hr className="my-4" />
          {/* Scene Comfort */}
          <h4 className="mb-2 gilroy-medium">Scene Comfort</h4>
          {user.sceneComfort && user.sceneComfort.length > 0 ? (
            <ul className="info-text list-unstyled">
              {user.sceneComfort.map((scene, index) => (
                <li key={index} className="mb-1">
                  <span className="info-text-title me-2">
                    {String(index + 1).padStart(2, "0")}.
                  </span>{" "}
                  {scene}
                </li>
              ))}
            </ul>
          ) : (
            <p className="info-text">No scene comfort preferences listed.</p>
          )}
          <hr className="my-4" />
          {/* Experience */}
          <h4 className="mb-2 gilroy-medium">Experience</h4>
          {user.pastProjects && user.pastProjects.length > 0 ? (
            <div className="mb-0 info-text">
              {user.pastProjects.map((project, index) => (
                <div
                  key={index}
                  className="mb-4 p-3 border rounded shadow-sm bg-light"
                >
                  <p className="mb-1">
                    <span className="info-text-title d-inline-block me-2">
                      {String(index + 1).padStart(2, "0")}.
                    </span>{" "}
                    <strong>{project.projectTitle}</strong>
                  </p>
                  <p className="mb-1">
                    <small className="text-muted">
                      {project.position}, {project.projectType}
                    </small>
                  </p>
                  <p className="mb-1">
                    <small className="text-muted">
                      {new Date(
                        project.projectDurationStartTime
                      ).toLocaleDateString("en-GB", {
                        day: "2-digit",
                        month: "short",
                        year: "numeric",
                      })}{" "}
                      -{" "}
                      {new Date(
                        project.projectDurationEndTime
                      ).toLocaleDateString("en-GB", {
                        day: "2-digit",
                        month: "short",
                        year: "numeric",
                      })}
                    </small>
                  </p>
                  <p className="mb-0">{project.projectDescription}</p>
                </div>
              ))}
            </div>
          ) : (
            <p className="info-text">No past projects listed.</p>
          )}
          <hr className="my-4" />
          {/* Social Links */}
          <h4 className="mb-3 gilroy-medium">Social Links</h4>
          {parsedSocials.length > 0 ? (
            <ul className="list-group mb-4">
              {parsedSocials.map((social, index) => (
                <li
                  key={index}
                  className="list-group-item d-flex align-items-center"
                >
                  <LinkIcon size={20} className="me-3 text-muted" />
                  <div>
                    <strong className="d-block">
                      {social.displayName || social.type}
                    </strong>
                    <a
                      href={social.url}
                      target="_blank"
                      rel="noreferrer noopener"
                      className="text-decoration-none"
                    >
                      {social.url}
                    </a>
                  </div>
                </li>
              ))}
            </ul>
          ) : (
            <p className="info-text">No social links provided.</p>
          )}
          <hr className="my-4" />
          {/* Media Section */}
          <h4 className="mb-3 gilroy-medium">Media</h4>
          {hasImages || hasVideos ? (
            <>
              {/* Images */}
              {hasImages && (
                <div className="mb-4">
                  <h5 className="mb-3 gilroy-bold d-flex align-items-center">
                    <ImageIcon size={20} className="me-2 text-primary" /> Images
                  </h5>
                  <div className="row g-3">
                    {" "}
                    {/* Use Bootstrap grid for images */}
                    {user.media.images.map((imageUrl, index) => (
                      <div key={index} className="col-md-4 col-lg-3">
                        {" "}
                        {/* Responsive columns */}
                        <a href={imageUrl} target="_blank" rel="noreferrer">
                          <img
                            src={imageUrl}
                            alt={`User media ${index + 1}`}
                            className="img-fluid rounded shadow-sm w-100" // img-fluid and w-100 for responsiveness
                            style={{ height: "200px", objectFit: "cover" }} // Fixed height for consistency
                          />
                        </a>
                      </div>
                    ))}
                  </div>
                </div>
              )}

              {/* Videos */}
              {hasVideos && (
                <div className="mb-4">
                  <h5 className="mb-3 gilroy-bold d-flex align-items-center">
                    <Youtube size={20} className="me-2 text-danger" /> Videos
                  </h5>
                  <div className="row g-3">
                    {user.media.videos.map((videoPath, index) => {
                      // Your video data is just the ID, so construct the URL
                      const youtubeUrl = `https://www.youtube.com/watch?v=${videoPath}`;
                      const videoId = getYouTubeVideoId(youtubeUrl);
                      if (!videoId) {
                        console.warn("Invalid YouTube URL/ID:", youtubeUrl);
                        return null; // Skip invalid video entries
                      }
                      return (
                        <div key={index} className="col-md-6 col-lg-4">
                          {" "}
                          {/* Larger columns for videos */}
                          <div className="ratio ratio-16x9 shadow-sm rounded">
                            {" "}
                            {/* 16:9 aspect ratio */}
                            <iframe
                              src={`https://www.youtube.com/embed/${videoId}`}
                              title={`YouTube video player ${index + 1}`}
                              frameBorder="0"
                              allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                              allowFullScreen
                              className="rounded"
                            ></iframe>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              )}
            </>
          ) : (
            <p className="info-text">No media (images or videos) provided.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default UserProfile;
